.palette-container {
    display: flex;
    flex-direction: column;
    padding: 25px;
    transition: box-shadow 0.2s ease;
    border-radius: 10px;
}

.palette-container:hover {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.palette {
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
}

.hex-code {
    font-size: 12px;
    border-radius: 10px;
    padding: 10px;
    color: #fff;
    transition: background-color 0.5s ease;
}

.color {
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
    height: 50px;
    cursor: pointer;
}

.color:hover .hex-code {
    background-color: #5e5e5e33;
}

.c0 {
    border-radius: 10px 10px 0 0;
}

.c3 {
    border-radius: 0 0 10px 10px;
}

h3 {
    margin-bottom: 10px;
}

.fav {
    display: flex;
    text-align: center;
    height: 34px;
    width: 34px;
    justify-content: center;
    align-items: center;
    border: 1px solid #e9e9e9;
    padding: 5px;
    border-radius: 5px;
    transition: background-color 0.2s ease;
}

.heart {
    color: #fd5454;
}

.fav:hover {
    cursor: pointer;
    background-color: #f8f8f8;
}

.palette-actions {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 10px;
}

.btn-see-more {
    margin-top: 10px;
    text-decoration: none;
    border: 1px solid #e9e9e9;
    padding: 10px;
    font-size: 12px;
    border-radius: 5px;
    color: #000;
    transition: color 0.2s ease, background-color 0.2s ease;
}

.btn-see-more:hover {
    cursor: pointer;
    background-color: rgb(37, 37, 37);
    color: #fff;
}